import { AuthProvider } from './../../providers/auth/auth';
import { LoadingProvider } from './../../providers/loading/loading';
import { RestProvider } from './../../providers/rest/rest';
import { Component } from '@angular/core';
import { IonicPage, NavController, AlertController } from 'ionic-angular';
import { GlobalProvider } from '../../providers/global/global';

/**
 * Generated class for the CheckOutPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-check-out',
  templateUrl: 'check-out.html',
})
export class CheckOutPage {
  note: string;
  sum: number;
  constructor(
    public navCtrl: NavController, 
    public global: GlobalProvider,
    public rest: RestProvider,
    public loading: LoadingProvider,
    public alert: AlertController,
    public auth: AuthProvider,
    ) {
    
  }

  ionViewWillEnter(){
    if (!this.auth.authenticated()) {
      this.auth.logout();
      this.navCtrl.setRoot('LoginPage');
      return;
    }

    this.sum = 0;
    this.global.productCart.forEach(element => {
      this.sum += element.price * element.quantity;
    });
  }

  goback() {
    this.navCtrl.pop();
  }

  sendCart() {
    this.loading.show();
    this.rest.sendCartOrder(this.global.productCart, this.note).then(
      (scs) => {
        console.log(scs);
        this.loading.hide();
        let alertSuccessfulOrder = this.alert.create({
          title: "Thành công",
          message: "Đơn hàng của bạn đã được gửi lên.",
          buttons: [
            {
              text: 'Ok',
              role: 'ok',
              handler: () => {
                this.global.productCart = [];
                this.note = '';
                this.navCtrl.popToRoot();
              }
            }
          ]
        });
        alertSuccessfulOrder.present();
      },
      (err) => {
        console.log(err);
        this.loading.hide();
      }
    )
    
  }

  presentNotifications(myEvent) {
    // let popover = this.popoverCtrl.create('NotificationPage');
    // popover.present({
    //   ev: myEvent
    // });
    this.navCtrl.push('NotificationPage');
  }

}



